$(document).ready(function () {
    var iconArrowUp = $(".ion-arrow-up-b"),
        linkCollapse = $(".sidebar-left .btn-primary"),
        tmenu = $('.menu-top'),
        navbarToggler = $('.navbar-toggler'),
        lpanelSidebar = $('.sidebar'),
        lpanelSidebarHeight,
        tmenuOffsetT;

    function synchronizeTMenuOffsetTop() {
        tmenuOffsetT = tmenu.offset().top;
    }

    synchronizeTMenuOffsetTop();

    // фиксируем меню при скроле

    function menuScroll() {
        $(window).scroll(function () {

            var wScroll = $(this).scrollTop();
            if ($(this).width() >= 747) {
                if (wScroll > tmenuOffsetT) {
                    $(tmenu).addClass('fixed-top');
                }
                if (wScroll < tmenuOffsetT) {
                    $(tmenu).removeClass('fixed-top');
                }
            } else {
                $(tmenu).removeClass('fixed-top');
            }
        });
    }

    // при  кнопку с icon в верхнем меню  до -lg- ( размер ширины document )

    $(navbarToggler).click(function () {

        if ($(this).attr("aria-expanded") == "false") {
            $(this).addClass("moveIconToggler");
            return;
        } else {
            $(this).removeClass("moveIconToggler");
            return;
        }
    });

    // открываем список товаров в левой панели  до -lg- ( размер ширины document )

    function listSidebarShow() {
        var listOfProds = $(".sidebar-left");
        if ($(window).width() >= 971) {
            $(listOfProds).find($(".collapse")).addClass("show");
        } else if ($(window).width() < 971) {
            $(listOfProds).find($(".collapse")).removeClass("show");
        }
    };

    // фиксируем левую панель при скроле

    function sidebarScroll() {
        var $right = $('.sidebar'),
            $left = $('.-under-body').children(".row"),
            leftHeight = $left.height() + $left.offset().top,
            scrollTop = $(window).scrollTop(),
            rightHeight = $right.height(),
            menuTopHeight = $('.menu-top').height(),
            menuHeight = $('.-under-body').children(".container-fluid").height() + $('.-under-body').children(".main-topic").height() - 25,
            distanceToBottom = $left.height() - rightHeight;
        console.log(menuTopHeight);
        if ($(window).width() >= 971) {
            if (scrollTop > menuHeight) {
                if (scrollTop + rightHeight + menuTopHeight + 25 > leftHeight) {
                    $right.css({
                        'position': 'relative',
                        'top': distanceToBottom + 'px'
                    });
                } else {
                    $right.css({
                        'position': 'fixed',
                        'top': menuTopHeight + 25 + 'px'
                    });
                }
            } else {
                $right.css({
                    'position': 'relative',
                    'top': '0px'
                });
            }
        }
    }

    // icon вращается с rotate  при collapse левой панели

    $(linkCollapse).click(function () {

        if (linkCollapse.attr("aria-expanded") === "false") {
            iconArrowUp.addClass("rotate180");
        } else {
            iconArrowUp.removeClass("rotate180");
        }
    });

    $(window).bind('scroll resize width', listSidebarShow, sidebarScroll);

    listSidebarShow();
    menuScroll();
    sidebarScroll();

    $(window).resize(function () {
        $(window).width(menuScroll, listSidebarShow);
    });
});