
$(document).ready(function () {
    var constImg;

    function constImgFunc() {
        if ($(document).width() <= 571) {
            constImg = 3;
        } else if ($(document).width() >= 571) {
            constImg = 4;
        } else if ($(document).width() >= 1179) {
            constImg = 5;
        }
    }

    function slickCarousel() {
        $('.slider-for').slick({
            slidesToShow: 1,
            slidesToScroll: 1,
            fade: true,
            asNavFor: '.slider-nav'

        });

        $('.slider-nav').slick({
            infinite: true,
            slidesToShow: constImg,
            slidesToScroll: 1,
            asNavFor: '.slider-for',
            dots: false,
            centerMode: false,
            focusOnSelect: true,
            initialSlide: 2,
            arrows: true

        });
    }
    constImgFunc();
    slickCarousel();

    $(window).bind('scroll resize', constImgFunc, slickCarousel);
});